import Vue from 'vue'
import VueRouter from 'vue-router'
//导入进度条
import nprogress from '@/nprogress/nprogress.js'
Vue.use(VueRouter)
const Home = () => import('@/views/home/Home.vue')
const Drag = () => import('@/components/js/drag/Drag.vue')
const jindouyun = () => import('@/components/js/jindouyun/jindouyun.vue')
const Books = () => import('@/components/js/books/Books.vue')
const Tabber = () => import('@/components/js/tabber/Tabber.vue')
const Guess = () => import('@/components/js/guess/Guess.vue')
const Swiper = () => import('@/components/js/swiper/Swiper.vue')

const Shape = () => import('@/components/css/shape/Shape.vue')
const Center = () => import('@/components/css/center/Center.vue')
const sanlan = () => import('@/components/css/sanlan/sanlan.vue')
const Scale = () => import('@/components/css/scale/Scale.vue')
const Nav = () => import('@/components/css/nav/Nav.vue')
 
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: 'Drag',
    children: [
      {
        path: '/Drag',
        name: 'Drag',
        component: Drag,
      },
      {
        path: '/jindouyun',
        name: 'jindouyun',
        component:jindouyun,
      },
      {
        path: '/Books',
        name: 'Books',
        component:Books,
      },
      {
        path: '/Tabber',
        name: 'Tabber',
        component:Tabber,
      },
      {
        path: '/Guess',
        name: 'Guess',
        component:Guess,
      },
      {
        path: '/Swiper',
        name: 'Swiper',
        component:Swiper,
      },
      {
        path: '/Center',
        name: 'Center',
        component:Center,
      },
      {
        path: '/sanlan',
        name: 'sanlan',
        component:sanlan,
      },
      {
        path: '/Shape',
        name: 'Shape',
        component:Shape,
      },
      {
        path: '/Scale',
        name: 'Scale',
        component:Scale,
      },
      {
        path: '/Nav',
        name: 'Nav',
        component:Nav,
      },
      
    ] 
  },
  {
    path: '/Home',
    name: 'Home',
    component: Home,
    
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})
//进度条
//全局路由前置守卫在跳转之前执行
router.beforeEach((to, from , next) => {
  // 每次切换页面时，调用进度条
  nprogress.start();

  // 这个一定要加，没有next()页面不会跳转的。这部分还不清楚的去翻一下官网就明白了
  next();
});
//全局路由后置守卫在跳转之后判断不会改变路由跳转没有next参数
router.afterEach(() => {
  window.scrollTo(0,0);
  // 在即将进入新的页面组件前，关闭掉进度条
  nprogress.done()
})
export default router
